import axios from 'axios';
import store from '../store';

import { Message } from 'ant-design-vue'

// axios
var http = axios.create({ timeout: 1000 * 12 ,withCredentials: true});
/** 
 * 
 */
const tipError = err => {  
  Message.error(err)
}
const logError = err => {
  console.log(err);  
}

// http request 

http.interceptors.request.use(
  config => {
    const token = store.state.common.token.str;//localStorage.token    
    token && (config.headers.Authorization = 'Basic ' + token);
    return config;
  },
  error => {
    return Promise.reject(error);
  }
);

// http response 
// 
http.interceptors.response.use(
  //200 
  res => {
    store.commit('NETWORK', true)
    if(res.status === 200){      
      return Promise.resolve(res)
    }else{
      return Promise.reject(res)
    }
  },
  error => {
    const { response } = error;
    if (response) {
      // 2xx 
      store.commit('NETWORK', true)
      errorHandle(response.status, error.config, response.data.message);
      return Promise.reject(response);
    } else {
      //       
      if (store.state.common.network || error.config.method != "get"){
        tipError("QiarAI");
      }
      store.commit('NETWORK', false)
    }
  }
);
/** 
 *  
 * @param {Number} status 
 */
const errorHandle = (status, config, other) => {
  //   
  switch (status) {
    // 401: token
    case 400:
      //console.log(config);
      tipError(other);
      break;
    case 401:
      //logError(other);
      break;
    // 403 token
    // token
    case 403:
      //logError('token');
      break;
    // 404
    case 404:
      logError('');
      break;
    default:
      //console.log(other);
  }
}

export default http;